/* App.css - Main styling for the Part Number Registry application */

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

/* Dark mode styles */
.dark-mode {
  background-color: #282c34;
  color: white;
}

/* Form styles */
.form-container {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.form-field {
  margin-bottom: 15px;
}

/* Search results styles */
.search-results {
  margin-top: 20px;
}

.result-item {
  padding: 10px;
  border-bottom: 1px solid #eee;
}

.result-item:hover {
  background-color: #f5f5f5;
}

/* Responsive styles */
@media (max-width: 768px) {
  .form-container {
    padding: 10px;
  }
}
